<template>
    <div>
        <div class="newList container">
            <ul>
                <li v-for="(item,index) in newsArr" :key="index">
                    <a href="javascript:;">
                    <div class="row">
                        <div class="col-md-12 col-sm-12 col-xs-12">
                            <span>来源：{{item.source}}</span>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12 col-sm-12 col-xs-12 newscont">
                            <a href="javascript:;">{{item.title}}</a>
                            <span>{{item.ptime}}</span>
                        </div>
                    </div>
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data() {
    return {
      newsArr: [
        // {
        //   sour: "Hacker News",
        //   cont: "wwagngsggsfdwedge",
        //   datetime: "2018/11/20"
        // },
        // {
        //   sour: "Hacker News",
        //   cont: "wwagngsggsfdwedge",
        //   datetime: "2018/11/20"
        // },
        // {
        //   sour: "Hacker News",
        //   cont: "wwagngsggsfdwedge",
        //   datetime: "2018/11/20"
        // }
      ]
    };
  },
  methods:{
      getData:function(){
          axios.post("https://www.apiopen.top/journalismApi").then(resp=>{
            //  axios.post("/staging/?url=https://ww//://www.baidu.com&que&query=.mnav:nth-child(3)").then(resp=>{
            //   https://yo08s2qyh9.execute-api.us-west-2.amazonaws.com/staging/?url=https://ww//://www.baidu.com&que&query=.mnav:nth-child(3)
            //   console.log(resp.data);
              this.newsArr=resp.data.data.auto;

          }).catch(err=>{

          });
      }
  },
  mounted() {
      this.getData();
  },
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
.newList li {
  /* background-color:#E2DFDF6EBEEC;
         */
  background-color: #B8D6FF;
  margin-bottom: 10px;
  padding: 10px 20px;
}
.newList li:hover {
  background-color: #6ebeec;
}
.newList li .row {
  text-align: left;
}
.newList li > a {
  color: #2c3e50;
}
.newscont > a {
  color: #2c3e50;
  margin-right: 20px;
}
</style>




