<template>
    <div>
       <div class="container">
           <!-- <el-row :gutter="10"> -->
               <!-- <el-col :xs="8" :sm="8" :md="8"> -->
                   <!-- <div> -->
                       <div class="row timetxt">
                           <div class="col-md-12 col-sm-12 col-xs-12">
                                 <span>选择关注的时间：</span>
                           </div>
                        </div>
                        <el-date-picker
                            v-model="value5"
                            type="datetimerange"
                            :picker-options="pickerOptions2"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            @change="chooseTime"
                            align="right">
                        </el-date-picker>
                   <!-- </div> -->
               <!-- </el-col> -->
           <!-- </el-row> -->
       </div>
    
    </div>
</template>
<script>
export default {
  data() {
    return {
      pickerOptions2: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
              console.log(start, end)
            }
          }
        ]
      },
      // value4: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
      value5: ""
    };
  },
  methods:{
    chooseTime(dt){
      console.log(dt)
    }
  }
};
</script>
<style>
.el-date-range-picker {
  /* left:-50%; */
  /* transform: translateX(50%); */
  /* margin-left:20px; */
  width: 100%;
  min-width: 468px;
}
/* .el-date-range-picker.has-sidebar {
  width: 80%;
} */
/* .el-picker-panel [slot="sidebar"],
.el-picker-panel__sidebar {
  width: 20%;
} */
.el-date-range-picker .el-picker-panel__body {
  min-width: 468px;
}
.el-date-editor--datetimerange.el-input,
.el-date-editor--datetimerange.el-input__inner {
  width: 60%;
}
.timetxt {
  text-align: left;
  padding-left: 20px;
  margin-bottom:20px;
}
.timetxt span {
    padding:5px 10px;
  background-color: #e287a7;
}
</style>




