import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

import Follow from "../pages/follow"
import Home from "../pages/home"
import Shouye from "../wraps/shouye"
import Shequ from "../wraps/shequ"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Follow',
      component: Follow
    },
    {
      path: '/follow',
      name: 'Follow',
      component: Follow,
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      children:[
        {
          path: 'shouye',
          name: 'Shouye',
          component: Shouye,
        },
        {
          path: 'shequ',
          name: 'Shequ',
          component: Shequ,
        },
        {
          path: '/',
          redirect: 'shouye',
        },
      ]
    },
  
  ]
})



