<template>
    <div>
        <div class="headWrap">
            <div class="container">
                 <nav class="navbar navbar-default">
                    <div class="container">
                        <div class="navbar-header">
                            <button class="navbar-toggle" data-toggle="collapse" data-target=".collapse">
                                <span class="icon-bar"></span>
                                <span class="icon-bar"></span>
                                <span class="icon-bar"></span>
                                <span class="icon-bar"></span>
                            </button>
                        </div>
                        <ul class="nav navbar-nav collapse navbar-collapse">
                            <router-link to="/home/shouye" tag="li" active-class="active"><a href="javascript:;">首页</a> </router-link>
                            <router-link to="/home/shequ" tag="li" active-class="active"><a href="javascript:;">社区</a></router-link>
                        </ul>
                    </div>
                </nav>
            </div>
        </div>
        <router-view></router-view>
         <div class="linkto-follow">
            <div class="row">
                <div class="col-md-12 col-sm-12 col-xs-12">
                    <a href="javascript:;" @click="link()">返回重设</a>
                </div>  
            </div>
        </div>
    </div>
</template>
<script>
export default {
  methods: {
    link: function() {
      this.$router.push("/follow");
    }
  }
};
</script>
<style>
.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .active > a:focus {
  background-color: #6ebeec;
}
.linkto-follow a {
  font-size: 20px;
  color: #6ebeec;
  text-decoration: none;
}
</style>



