<template>
    <div>
        <div class="container">
            <div class="row tit">
                <div class="col-md-12 col-sm-12 col-xs-12">
                    <a href="javascript:">感兴趣</a>
                </div>
            </div>
            <div class="content">
               <Followitem></Followitem>
            </div>
            <div class="linkto-shouye">
                <div class="row">
                    <div class="col-md-12 col-sm-12 col-xs-12">
                        <span>选好了？</span>
                        <a href="javascript:;" @click="link()">进入首页</a>
                    </div>  
                 </div>
            </div>
        </div>
    </div>
</template>
<script>
import Followitem from "./followitem"
export default {
    components:{
        Followitem
    },
    methods:{
        link:function(){
            this.$router.push('/home');
        }
    }

};
</script>
<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.tit {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  text-align: left;
  margin-bottom: 30px;
}
.tit a {
  font-size: 20px;
  color: #6ebeec;
  text-decoration: none;
}
.content{
    border-bottom:1px solid #ccc;
    margin-bottom:20px;
}
.linkto-shouye{
    /* border-bottom:1px solid #ccc; */
      margin-bottom:20px;
}
.linkto-shouye span{
    font-size:16px;
}
.linkto-shouye a{
    font-size: 20px;
  color: #6ebeec;
  text-decoration: none;
}
</style>



