<template>
    <div>
        <div class="row" v-for="(item,index) in listArr" :key="index">
            <div class="col-md-2 col-sm-4 col-xs-6 sources-div"  v-for="(item,indexin) in item.sorlist" :key="indexin">
                <div class="sources">
                    <div class="row">
                    <div class="col-md-12 col-sm-12 col-xs-12">
                        <img :src="item.logos" alt="">
                        <!-- <img src="../assets/images/hacker-news.png" alt=""> -->
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 col-sm-12 col-xs-12">
                        <span>{{item.source}}</span>
                        <!-- <span>Hacker News</span> -->
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 col-sm-12 col-xs-12">
                        <button class="btn folBtn" :class="{'btn-primary':item.btnflag,'btn-default':!item.btnflag}" txtFlag='0' @click="sel(index,indexin)">订阅</button>
                    </div>
                </div>     
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import logo01 from '../assets/images/hacker-news.png'
import logo02 from '../assets/images/GitHub.png'
import logo03 from '../assets/images/csdn.png'
import logo04 from '../assets/images/CN_jianshu.png'
import logo05 from '../assets/images/bokeyuan.png'
import logo06 from '../assets/images/stack-overflow.png'
import logo07 from '../assets/images/stack-overflow.png'
import logo08 from '../assets/images/stack-overflow.png'
export default {
    data(){
        return{
            urlList:[],
            listArr:[
                 // 每个list中有6个元素，超过6个换行
            {sorlist:[
                {
                    logos:logo01,
                    source:"Hacker News",
                    btnflag:true,
                    url:"https://news.ycombinator.com/"
                },
                  {
                    logos:logo02,
                    source:"GitHub",
                     btnflag:true,
                     url:"https://github.com/"
                },
                {
                    logos:logo03,
                    source:"CSDN",
                    btnflag:true,
                    url:"https://www.csdn.net/"
                    
                },
                  {
                    logos:logo04,
                    source:"简书",
                    btnflag:true,
                    url:"https://www.jianshu.com/"
                },
                  {
                    logos:logo05,
                    source:"博客园",
                    btnflag:true,
                    url:"https://www.cnblogs.com/"
                },
                  {
                    logos:logo06,
                    source:"stack overflow",
                    btnflag:true,
                    url:"https://stackoverflow.com/"
                }
            ]},  
              {sorlist:[
                  {
                    logos:logo07,
                    source:"stack overflow",
                    btnflag:true,
                    url:"https://stackoverflow.com/"
                },
                    {
                    logos:logo08,
                    source:"stack overflow",
                    btnflag:true,
                    url:"https://stackoverflow.com/"
                },
            ]}
            ]
           

        }
    },
    methods:{
        sel:function(index,indexin,btnflag){
            var urlList=[];
            this.listArr[index].sorlist[indexin].btnflag=!this.listArr[index].sorlist[indexin].btnflag;
            if(!this.listArr[index].sorlist[indexin].btnflag){
                 this.urlList.push(this.listArr[index].sorlist[indexin].url);
                 console.log(this.listArr[index].sorlist[indexin].btnflag)
            }
            else{
                var urlIndex=this.urlList.indexOf(this.listArr[index].sorlist[indexin].url);
                 if(urlIndex>-1){
                      this.urlList.splice(urlIndex,1);
                 }
            }
            console.log( this.urlList);
        }
    },
}
$(function(){
$(".folBtn").click(function(){
    var i=$(this).attr("txtFlag");
                // console.log(i);
                if(i==0){
                    $(this).attr("txtFlag","1");
                    $(this).html("")
                    $(this).html("取消");
                }
                else{
                  $(this).attr("txtFlag","0");
                    $(this).html("")
                    $(this).html("订阅");
                }

            })
})
</script>
<style>
.sources-div{
    padding:20px;
}
.sources {
  border: 1px solid #888;
  border-radius: 5px;
  padding: 5px;
  /* margin-right:20px; */
}
.sources span {
    width:100%;
    display:block;
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.sources > .row {
  margin-bottom: 10px;
}
.sources > .row:last-child {
  margin-bottom: 0;
}
</style>



