<template>
    <div>
        <div class=row>
            <div class="col-md-12 col-sm-12 col-xs-12">
                <Datetime @pick="getDatetime"  class="datetimer"></Datetime>
            </div>
        </div>
        
        <News></News>
    </div>
</template>
<script>
import Datetime from "../components/datetime"
import News from "../components/news"
export default {
    components:{
        Datetime,
        News
    },
    methods:{
        getDatetime:function(val){
            console.log(val)
        }
    }
}
</script>
<style>
   .datetimer{
       margin-bottom:20px;
   } 
</style>



